.cardCity {
  border: none;
  height: 100%;
  width: 100%;
  overflow: hidden;
  border-radius: 13px;
}

.cardCity .img-block {
  height: 100%;
}

.cardCity .img-block .card-img {
  border-radius: 13px;
  height: 100%;
  width: 100%;
  display: block;
  transition: all 1s;
}

.cardCity:hover .card-img {
  transform: scale(1.1);
}

.cardCity .card-country {
  color: white;
  font-size: 0.9em;
  font-weight: 500;
  position: absolute;
  top: 1.25em;
  left: 1.25em;
  text-transform: uppercase;
}

.cardCity .card-city {
  position: absolute;
  bottom: 1.5em;
  left: 1.25em;
}

.cardCity .card-city .card-city-name {
  font-size: 1.2em;
  font-weight: 600;
}

.cardCity .card-city .card-city-placeNums {
  font-size: 0.8em;
  color: white;
}

.cardCity .card-city .card-city-name a {
  color: white;
}

.cardCity .card-city .card-city-name a:hover {
  color: #24d8d8;
  text-decoration: none;
}

.card-place {
  height: 100%;
  width: 100%;
  border-radius: 13px;
  overflow: hidden;
}

.card-place .card-place-thumbnail {
  overflow: hidden;
}

.card-place .review-star-pic {
  width: 18px;
  height: 18px;
  margin-left: 5px;
  margin-bottom: 5px;
}

.card-place .card-place-img {
  width: 100%;
  height: 135px;
  object-fit: cover;
  border-radius: 0px;
  transition: all 1s;
}

.card-place .card-place-img:hover {
  transform: scale(1.1);
}

.card-place .card-place-term {
  display: flex;
  font-size: 12px;
  font-weight: 500;
}

.card-place .card-place-term a {
  text-decoration: none;
  color: #212529;
}

.card-place .card-place-term a:hover {
  color: #24d8d8;
}

.card-place .card-place-term .place-type {
  text-transform: capitalize;
}

.card-place .card-place-term .place-city {
  position: relative;
  padding-left: 10px;
  margin-left: 8px;
  text-transform: capitalize;
}

.card-place .card-place-term .place-city::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 6px;
  width: 3px;
  height: 3px;
  border-radius: 3px;
  background-color: #d8d8d8;
}

.card-place .card-title a {
  color: black;
}

.card-place .card-title a:hover {
  color: #24d8d8;
  text-decoration: none;
}

.review-detail {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  padding-bottom: 20px;
  padding-top: 5px;
}

.review-detail .review-author-block {
  display: flex;
  margin-top: 20px;
  margin-right: 20px;
}

.review-detail .review-author-headpic {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 20px;
}

.review-detail .review-content {
  margin-top: 10px;
  color: #5d5d5d;
}

.review-detail .review-star-block {
  display: flex;
}

.review-detail .review-star-block .author-name {
  font-size: 17px;
}

.journal-card {
  padding: 1.25rem;
  padding-top: 0;
  border: none;
  margin-top: 10px;
}

.journal-card .journal-title {
  display: flex;
}

.journal-card .journal-title .journal-title-loveNum-block {
  position: absolute;
  right: 2rem;
  color: #5d5d5d;
  border: 1px solid #24d8d8;
  padding-left: 10px;
  padding-right: 0px;
  margin-top: 5px;
  border-radius: 50px;
}

.journal-card
  .journal-title
  .journal-title-loveNum-block
  .journal-title-loveNum {
  color: #24d8d8;
}

.journal-card .journal-title .journal-title-loveNum-block .journal-title-icon {
  margin-bottom: 4px;
}

.journal-card .journal-title .journal-title-icon {
  width: 15px;
  height: 15px;
  margin-right: 10px;
}

.journal-card .journal-title .journal-title-content {
  margin: 0;
  font-size: 16px;
  line-height: 100%;
}

.journal-card .journal-title .journal-title-content .journal-title-type {
  margin-left: 5px;
  color: #24d8d8;
  font-style: italic;
}

.journal-card .card-title {
  margin-top: 10px;
}

.journal-card .journal-hr {
  margin-top: 0;
  border-top: 2px solid #f8f8f8;
}

.journal-card .journal-link {
  color: black;
  text-decoration: none;
}

.journal-card:hover {
  color: black;
  text-decoration: none;
  background-color: #f8f8f8;
}

.journal-card:hover .card-title {
  color: #24d8d8;
}

.journal-card .journal-content-img {
  width: 100%;
  height: 120px;
  max-height: 200px;
  object-fit: cover;
}

.journal-card .journal-item-block {
  margin-top: 15px;
}

.journal-card .journal-item-description {
  color: #5d5d5d;
  position: relative;
  word-wrap: break-word;
  overflow: hidden;
}

.journal-card .journal-author-block .journal-item-details {
  font-size: 10px;
  font-style: italic;
}

.journal-card .journal-author-block {
  margin-top: 20px;
  margin-right: 30px;
  float: right;
}

.journal-card .journal-author-block .journal-author-pic {
  width: 11px;
  height: 11px;
  object-fit: fill;
  margin-right: 10px;
}

.journal-card .journal-author-block .journal-item-username {
  color: #24d8d8;
  font-size: 13px;
}

.journal-card .journal-author-block .journal-icon-views {
  width: 11px;
  height: 11px;
  margin-left: 4px;
}

.journal-card .journal-author-block .journal-item-views {
  font-size: 10px;
  color: #5d5d5d;
}

.topic-card {
  border: none;
}

.topic-card .topic-card-img {
  height: 230px;
  object-fit: cover;
  border-radius: 10px;
}

.topic-card .topic-cardbody {
  padding: 0;
  padding-top: 1em;
}

.topic-card .topic-cardbody .topic-title {
  font-size: 20px;
  font-weight: 600;
}

.topic-card .topic-cardbody .topic-title a {
  color: #2d2d2d;
  text-decoration: none;
}

.topic-card .topic-cardbody .topic-title a:hover {
  color: #24d8d8;
}

.topic-card .topic-taglist {
  margin-left: 0;
  padding-left: 0;
}

.topic-card .topic-taglist li {
  list-style: none;
  display: inline;
  margin-right: 1em;
}

.topic-card .topic-taglist li a.topic-tag {
  text-transform: uppercase;
  color: black;
  text-decoration: none;
  font-weight: 500;
  font-size: 13px;
  border-bottom: 1px solid #24d8d8;
  cursor: pointer;
}

.topic-card .topic-taglist li a.topic-tag:hover {
  color: #24d8d8;
  cursor: pointer;
}

.topic-card .topic-author-info {
  font-size: 15px;
  line-height: 23px;
  font-style: italic;
  margin-bottom: 20px;
  display: flex;
  flex-wrap: wrap;
  font-weight: 500;
  color: #8d8d8d;
}

.topic-card .topic-author-name {
  color: #5d5d5d;
}

.topic-card .topic-author {
  margin-right: 15px;
  position: relative;
}

.topic-card .topic-author-info span {
  margin-right: 10px;
}

.topic-card .topic-author-name::after {
  content: "";
  width: 4px;
  height: 4px;
  background-color: #d8d8d8;
  -webkit-border-radius: 30px;
  -moz-border-radius: 30px;
  border-radius: 30px;
  display: block;
  position: absolute;
  right: -5px;
  top: 50%;
  margin-top: -2px;
}

.topic-card .topic-content {
  margin-bottom: 25px;
  line-height: 1.8;
  color: #5d5d5d;
}

.topic-card .topic-readmore-btn a {
  text-decoration: none;
  position: relative;
  display: inline-block;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  padding-bottom: 3px;
  border-bottom: 1px solid #ddd;
  color: #2d2d2d;
}

.topic-card .topic-readmore-btn:hover a {
  color: #24d8d8;
  border-bottom: 1px solid #24d8d8;
}

.comment-detail {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  padding-bottom: 20px;
  padding-top: 10px;
}

.comment-detail .comment-author-block {
  display: flex;
  margin-top: 20px;
  margin-right: 20px;
}

.comment-detail .comment-author-headpic {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 20px;
}

.comment-detail .comment-content {
  margin-top: 10px;
  color: #5d5d5d;
}

.star-rating {
  unicode-bidi: bidi-override;
  color: #ddd;
  font-size: 0;
  height: 20px;
  margin: 0 auto;
  position: relative;
  display: table;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 0px;
}

.star-rating span {
  padding: 3px;
  font-size: 18px;
}

.star-rating span:after {
  content: "★";
}

.star-rating .star-rating-top {
  color: #24d8d8;
  position: absolute;
  z-index: 1;
  display: block;
  top: 0;
  left: 0;
  overflow: hidden;
  white-space: nowrap;
}

.star-rating .star-rating-bottom {
  display: block;
  z-index: 0;
}

@media only screen and (max-width: 575px) {
  .journal-card .journal-content-img {
    display: none;
  }
}
