.topic-top {
  margin-top: 70px;
}

.topic-top .topic-top-block {
  height: 180px;
  width: 100%;
  display: flex;
}

.topic-top .topic-top-left {
  display: block;
  width: 100%;
  z-index: 1;
  background-color: #24d8d8;
}

.topic-top .topic-top-left .topic-note {
  color: white;
  font-weight: 600;
  margin-left: 10%;
  margin-top: 30px;
}

.topic-top .topic-top-left .topic-slogan {
  color: white;
  margin-left: 10%;
  margin-top: 20px;
  width: 40%;
  font-weight: 500;
}

.topic-top .topic-top-pic {
  position: absolute;
  right: 0;
  overflow: hidden;
  width: 60%;
  height: 180px;
  border-radius: 100px 0 0 100px;
  z-index: 2;
  object-fit: cover;
}

.topic-content-block {
  margin-top: 40px;
  margin-bottom: 40px;
}

.topic-content-block .topic-col {
  margin-top: 20px;
}

.topic-content-block .topic-nav-block {
  text-align: center;
  padding-bottom: 40px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.topic-content-block .topic-nav-block .list-category {
  font-size: 15px;
  line-height: 32px;
  text-transform: uppercase;
  list-style-type: none;
  display: table;
  padding-left: 0;
  margin-bottom: 0;
  font-weight: 600;
}

.topic-content-block .topic-nav-block .list-category li {
  display: inline-block;
  padding-left: 12px;
  padding-right: 12px;
}

.topic-content-block .topic-nav-block .list-category li a {
  color: #2d2d2d;
  transition: all 0.3s;
}

.topic-content-block .topic-nav-block .list-category li:hover a {
  color: #24d8d8;
  text-decoration: none;
}

.topic-detail-top-pic {
  object-fit: cover;
  height: 450px;
  margin-top: 56px;
}

.topic-details-main {
  margin-top: 25px;
  margin-bottom: 30px;
}

.topic-details-main .topic-category-list {
  margin-bottom: 15px;
}

.topic-details-main .topic-link {
  color: #2d2d2d;
  text-decoration: none;
  border-bottom: 1px solid #24d8d8;
  margin-right: 10px;
  text-transform: uppercase;
  transition: all 0.3s;
}

.topic-details-main .topic-link:hover {
  text-decoration: none;
  color: #24d8d8;
}

.topic-details-main .topic-basic-details {
  font-size: 15px;
  line-height: 23px;
  font-style: italic;
  margin-bottom: 20px;
  display: flex;
  flex-wrap: wrap;
  font-weight: 500;
  color: #8d8d8d;
}

.topic-details-main .topic-basic-details span {
  margin-right: 10px;
  font-style: italic;
  font-weight: 500;
}

.topic-details-main .topic-basic-details .topic-author-info {
  position: relative;
  margin-right: 15px;
}

.topic-details-main .topic-basic-details .topic-author-name {
  color: #5d5d5d;
}

.topic-details-main .topic-basic-details .topic-author-info::after {
  content: "";
  width: 4px;
  height: 4px;
  background-color: #d8d8d8;
  -webkit-border-radius: 30px;
  -moz-border-radius: 30px;
  border-radius: 30px;
  display: block;
  position: absolute;
  right: -5px;
  top: 50%;
  margin-top: -2px;
}

.topic-details-main .topic-paragragh p {
  color: #5d5d5d;
  line-height: 1.6;
  font-weight: 500;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.topic-details-main .topic-paragragh .topic-content-subtitle {
  color: black;
  font-weight: 500;
  font-size: 24px;
  text-transform: capitalize;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.topic-details-main .topic-paragragh .topic-paragraph-pic {
  max-width: 100%;
  border-radius: 5px;
}

.topic-details-main .topic-related-article {
  margin-top: 30px;
}

.topic-details-main .topic-related-article .topic-card-img {
  height: 240px;
  object-fit: cover;
}

.topic-details-main .topic-details-leftside .topic-comment-login {
  text-decoration: none;
  color: #24d8d8;
}

.topic-details-main
  .topic-details-leftside
  .topic-comment-block
  .topic-comment-note {
  font-size: 20px;
  margin-bottom: 15px;
  font-weight: 500;
}

.topic-details-main .topic-details-leftside .link-login {
  color: #24d8d8;
}

#topic-comment {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

.topic-details-rightside {
  padding-left: 20px;
  max-width: 400px;
}

.topic-details-rightside .topic-category-alllist .topic-category-ul {
  text-transform: capitalize;
  padding-left: 20px;
  margin-top: 20px;
}

.topic-details-rightside .topic-category-alllist .topic-category-ul li {
  margin-bottom: 10px;
  font-weight: 500;
}

.topic-details-rightside .topic-category-alllist .topic-category-ul li a {
  color: #5d5d5d;
}

.topic-details-rightside .topic-category-alllist .topic-category-ul li a:hover {
  color: #24d8d8;
}

.topic-details-rightside .topic-recommend-block {
  margin-bottom: 20px;
}

.topic-details-rightside .topic-category-alllist .topic-category-title,
.topic-details-rightside .topic-recommend-block .topic-recommend-title {
  color: #666;
}

@media screen and (max-width: 991px) {
  .topic-details-rightside {
    display: none;
  }
}

@media screen and (max-width: 767px) {
  .topic-top-pic {
    width: 50%;
    height: 130px;
  }
  .topic-top .topic-top-block {
    height: 130px;
  }
}
